import React, { Component } from 'react';

class AboutUs extends Component {
    render() {
        return (
            <section id="about" className="about-area ptb-100 bg-image">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-6 col-md-12">
                            <div className="row about-image">
                                <div className="col-6 col-lg-6 col-md-6">
                                    <div className="image">
                                        <img src={require("../../images/about-img1.jpg")} alt="about" />
                                    </div>
                                </div>

                                <div className="col-6 col-lg-6 col-md-6">
                                    <div className="image">
                                        <img src={require("../../images/about-img2.jpg")} alt="about" />
                                    </div>
                                </div>

                                <div className="col-6 col-lg-6 col-md-6">
                                    <div className="image mt-30">
                                        <img src={require("../../images/about-img3.jpg")} alt="about" />
                                    </div>
                                </div>

                                <div className="col-6 col-lg-6 col-md-6">
                                    <div className="image mt-30">
                                        <img src={require("../../images/about-img4.jpg")} alt="about" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                        <div className="col-lg-6 col-md-12">
                            <div className="about-content">
                                <span>Giới Thiệu</span>
                                <h2>Chúng tôi là <b>Ruby</b> Team</h2>
                                <p>Là đội ngũ có kinh nghiệm lâu dài trong lĩnh vực bất động sản với phong cách làm việc:</p>
                                 
                                <ul>
                                    <li>Chuyên nghiệp</li>
                                    <li>Tận tâm</li>
                                    <li>Đam mê</li>
                                    <li>Vui vẻ</li>
                                </ul>

                                <p>Chúng tôi cung cấp thông tin về các dự án bất động sản tại Lâm Đồng. Chúng tôi hợp tác với tất cả mọi người có nhu cầu tìm hiểu về bất động sản tại Lâm Đồng.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default AboutUs;